@import '../../style/vars';

.comic__item {
    width: 100%;
    padding: 0px 5px 200px 5px;
    display: flex;
    gap: 50px;
    justify-content: space-between;
    & .item-img {
        
        & img {
            width: 295px;
            height: 450px;
            object-fit: cover;
        }
    }
    
    & .item-text {
        max-width: 550px;
        height: 50%;
        display: flex;
        flex-direction: column;
        gap: 20px;

    }

    & .text {
        text-align: justify;
        font-size: 0.5rem;
        line-height: 21px;
        font-weight: normal;
        margin-bottom: 50px;
    }

    & .pages, & .lang, & .price {
        display: block;
        font-size: 0.5rem;
        font-weight: normal;
    }

    & .price {
        color: $main-color;
        font-size: 0.85rem;
    }

    & .title {
        font-size: 0.78rem;
    }

    & .back {
        color: #000;
        background: none;
        font-size: 0.64rem;
        font-weight: 700;
        height: 30px;
        transform: translateY(0);
        transition: all 0.4s ease;

        &:hover {
            cursor: pointer;
            color: $main-color;
            transform: translateY(2px);
        }
    }
}